
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
components: {
XHeader,
XTable,
LoadMore

},
data() {
return {
count: 0,
content: "0"
}
},
methods: {
radios: function() {
let _this = this;
_this.count = 0;
$("input:checked").each(function() {
_this.count = _this.count + Number($(this).val());
})
if(_this.count == "0") {
_this.content = "" + _this.count + "";
} else if(_this.count == "1") {
_this.content = "" + _this.count + "OAC";
} else if(_this.count >= "2") {
_this.content = "" + _this.count + "OAC";
}
}
}
}



