
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
components: {
XHeader,
XTable,
LoadMore
},
data() {
return {
count: 0,
conent:"2"
}
},
methods: {
radios: function() {
var _this = this;
_this.count = 0;
$("input:checked").each(function() {
_this.count = _this.count + Number($(this).val());
})
if(_this.count < "3"){
_this.conent = ""+_this.count+"";
}else if(_this.count >="3" && _this.count<="6"){
_this.conent = ""+_this.count+"";
}else{
_this.conent = ""+_this.count+"+";
}
}
}
}



