
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name: 'hello',
components: {
XHeader,
XTable,
LoadMore
},
data() {
return {
count:0,
conent:"50.39%"
}
},
methods: {
radios: function() {
var _this = this;
_this.count = 0 ;
$("input:checked").each(function() {
_this.count = _this.count + Number($(this).val());
})
_this.conent = ""+_this.count+"0.39%"

}
}
}



