
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
name: 'hello',
components: {
XHeader,
XTable,
LoadMore

},
data() {
return {
count:0,
conent:"00.3%"
}
},
methods:{
radios: function() {
var _this =  this;
_this.count = 0;
$("input:checked").each(function() {

_this.count = _this.count+Number($(this).val());


})
if(_this.count=="0"){
_this.conent=""+_this.count+"0.3%";
}else if(_this.count=="1"){
_this.conent=""+_this.count+"0.4%";
}else if(_this.count=="2"){
_this.conent=""+_this.count+"0.6%";
}else if(_this.count=="3"){
_this.conent=""+_this.count+"1%";
}else if(_this.count=="4"){
_this.conent=""+_this.count+"1.5%";
}else if(_this.count=="5"){
_this.conent=""+_this.count+"2.4%";
}else if(_this.count=="6"){
_this.conent=""+_this.count+", 3.7%"
}else if(_this.count=="7"){
_this.conent=""+_this.count+", 5.7%"
}else if(_this.count=="8"){
_this.conent=""+_this.count+", 8.7%"
}else if(_this.count=="9"){
_this.conent=""+_this.count+", 13%"
}else if(_this.count=="10"){
_this.conent=""+_this.count+", 19%"
}else if(_this.count=="11"){
_this.conent=""+_this.count+", 26.8%"
}else if(_this.count=="12"){
_this.conent=""+_this.count+", 36.5%"
}else if(_this.count=="13"){
_this.conent=""+_this.count+", 47.4%"
}else if(_this.count=="14"){
_this.conent=""+_this.count+", 58.6%)"
}else if(_this.count=="15"){
_this.conent=""+_this.count+", 68.9%)"
}else if(_this.count=="16"){
_this.conent=""+_this.count+", 77.7%)"
}else if(_this.count=="17"){
_this.conent=""+_this.count+", 84.5%)"
}else if(_this.count=="17"){
_this.conent=""+_this.count+", 89.5%)"
}
}
}
}



