
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
components: {
XHeader,
XTable,
LoadMore
},
data() {
return {
count: 0,
conent:"0-2SICH0.4%"
}
},
methods: {
radios: function() {
var _this =  this;
_this.count = 0;
$("input:checked").each(function() {

_this.count = _this.count+Number($(this).val());


})
if(_this.count >= 0 && _this.count <= 2){
_this.conent=""+_this.count+"SICH0.4%";
}else if(_this.count >= 3 && _this.count <= 5){
_this.conent=""+_this.count+"SICH1.5%";
}else if(_this.count >= 6 && _this.count <= 8){
_this.conent=""+_this.count+"SICH3.6%";
}else if(_this.count >= 9){
_this.conent=""+_this.count+"SICH9.2%";
}
}
}
}



